.sh-button {
  border-radius: 0px;
  border: thin solid transparent;
  font-size: 16px;
  font-weight: 400;
  transition: all 0.3s;
  text-decoration: none;
  line-height: 1;
  padding: 12px 16px;
}

.sh-button:hover {
  text-decoration: none;
  transform: translateY(-2px);
}

.sh-button--primary {
  background-color: #1f2020;
  color: #ffffff;
}

.sh-button--primary:hover {
  color: #ffffff;
}

.navbar {
  padding-top: 16px;
  padding-bottom: 16px;
}

.navbar.navbar-fixed {
  box-shadow: 0 1px 6px rgba(38, 38, 39, 0.05);
  -moz-box-shadow: 0 1px 6px rgba(38, 38, 39, 0.05);
  -webkit-box-shadow: 0 1px 6px rgba(38, 38, 39, 0.05);
}

@media screen and (max-width: 991px) {
  .navbar {
    box-shadow: 0 1px 6px rgba(38, 38, 39, 0.05);
    -moz-box-shadow: 0 1px 6px rgba(38, 38, 39, 0.05);
    -webkit-box-shadow: 0 1px 6px rgba(38, 38, 39, 0.05);
  }
}

.navbar .navbar-brand {
  margin: 0 24px 0 0;
}

.navbar .navbar-nav .nav-item .nav-link {
  margin: 0 16px;
  padding: 4px 0;
  transition: all 0.3s;
  border-bottom: 2px solid transparent;
}

@media screen and (max-width: 991px) {
  .navbar .navbar-nav .nav-item .nav-link {
    display: inline-block;
    margin: 16px 0 0;
  }
}

.navbar .navbar-nav .nav-item:hover .nav-link, .navbar .navbar-nav .nav-item:active .nav-link, .navbar .navbar-nav .nav-item:focus .nav-link, .navbar .navbar-nav .nav-item.active .nav-link {
  border-bottom: 2px solid #E81F26;
}

.navbar .navbar-right {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-direction: normal;
  -webkit-box-orient: horizontal;
  -webkit-flex-direction: row;
  -moz-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  -webkit-justify-content: flex-start;
  -moz-justify-content: flex-start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  -moz-align-items: center;
  align-items: center;
}

@media screen and (max-width: 991px) {
  .navbar .navbar-right {
    -webkit-box-direction: normal;
    -webkit-box-orient: vertical;
    -webkit-flex-direction: column;
    -moz-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: start;
    -ms-flex-align: start;
    -webkit-align-items: flex-start;
    -moz-align-items: flex-start;
    align-items: flex-start;
  }
  .navbar .navbar-right .sh-social--item {
    margin: 0 32px 0 0;
  }
}

@media (min-width: 991px) and (max-width: 1100px) {
  .navbar .navbar-right .sh-social--row {
    display: none !important;
  }
}

@media screen and (max-width: 991px) {
  .navbar .navbar-right .sh-social--row {
    margin: 32px 0;
  }
}

.navbar .navbar-right .sh-button {
  margin: 0 0 0 16px;
}

@media screen and (max-width: 991px) {
  .navbar .navbar-right .sh-button {
    margin: 0 0 16px;
  }
}

